import Vue from 'vue'
import Router from 'vue-router'
import H from '@/components/page/H'
import Pos from '@/components/page/Pos'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Pos',
      component: Pos
    },
    {
      path: '/H',
      name: 'H',
      component: H
    }
  ]
})



